/**
 * 
 *
 * @param {BoardOptions} options
 * @returns {BoardOptions}
 */
function defineOptions(options) {
  return options
}

/**
 * 
 * @param {Field[]} fields
 * @returns {Field[]}
 */
function defineFields(fields) {
  return fields || []
}

/**
 *
 * @param {Function<object>} renderer
 * @param {{x: number, y: number}} offset
 * @param {string} [trigger=click]
 * @returns
 */
function defineTooltip(renderer, offset, trigger) {
  offset = Object.assign({
    x: 10,
    y: 10
  }, offset)
  return {
    // offsetX  offsetY  padding
    offsetX: offset.x,
    offsetY: offset.y,
    trigger: trigger || 'click',
    shouldBegin() {
      //  tooltip 
      if (this.editMode) {
        return false
      }
      return true
    },
    getContent(e) {
      //  tooltip 
      if (this.editMode) {
        return ''
      }
      const data = e.item.getModel()
      const id = data.id

      // 
      const cache = window.__g6board_tooltip_cache || {}

      const now = new Date().getTime()
      if (cache[id]) {
        //  10 
        if (cache[id].time && now - cache[id].time < 10000) {
          return cache[id].element
        }
      }

      const container = document.createElement('div')
      cache[id] = {
        time: now,
        element: container
      }

      container.classList.add('g6-board--tooltip')

      const result = renderer(data, container)

      // 
      if (result instanceof Promise) {
        container.innerHTML = '<span> ...</span>'
        result.then(content => {
          if (typeof content === 'string') {
            container.innerHTML = content
          } else {
            container.innerHTML = ''
            container.appendChild(content)
          }
        })
      } else {
        if (typeof result === 'string') {
          container.innerHTML = result
        } else {
          container.appendChild(result)
        }
      }
      return container
    }
  }
}

