import { BUILTIN_FIELDS, InputTypes, defineFields } from "../components/models"
import storage from '../assets/storage'
// import lineTypes from "./lineTypes"
import { ColorPicker } from "element-ui"

const nodeFields = defineFields([{
  label: '',
  name: 'deviceType',
  inputType: InputTypes.SELECT,
  options: storage.getDeviceTypes(),
  config: {
    required: true,
    tip: '',
    onchange(e) {
      e.data.device = null
      e.fields.device.options = []
    },
  }
}, {
  label: '',
  name: 'device',
  inputType: InputTypes.SELECT,
  options: [],
  config: {
    tip: ' IP ',
    required: true,
    optionsLoader: function (e, keyword) {
      const deviceType = e.data.deviceType

      const data = {
        pc: [{
          label: '1111',
          value: '192.168.11.11'
        }, {
          label: '2222',
          value: '192.168.11.12'
        }, {
          label: '3333',
          value: '192.168.11.13'
        }],
        server: [{
          label: '1111',
          value: '192.168.12.11'
        }, {
          label: '2222',
          value: '192.168.12.12'
        }, {
          label: '3333',
          value: '192.168.12.13'
        }],
      }

      if (!deviceType) {
        // console.debug('')
        this.options = []
        return
      }

      const list = data[deviceType]

      if (!list) {
        // console.debug('')
        this.options = []
        return
      }

      if (!keyword) {
        // console.debug('')
        this.options = list
        return
      }

      // console.debug('')

      this.options = list.filter(item => {
        return item.label.indexOf(keyword) !== -1 || item.value.indexOf(keyword) !== -1
      })
      // },
      // onchange(e) {
      //   console.info(e)
    }
  }
}, {
  label: ' IP',
  name: 'ip',
  inputType: InputTypes.TEXT,
  config: {
    required: true,
    tip: 'IP '
  }
}, {
  label: '',
  name: 'label',
  inputType: InputTypes.TEXT,
  // }, {
  //   label: '',
  //   name: 'speed',
  //   inputType: InputTypes.NUMBER,
  // }, {
  //   label: '',
  //   name: 'enabled',
  //   inputType: InputTypes.SWITCH,
}, {
  label: '',
  name: 'labelColor',
  inputType: InputTypes.CUSTOM,
  component: ColorPicker
}, {
  label: '',
  name: 'remark',
  inputType: InputTypes.LONGTEXT,
  // }, {
  //   label: '',
  //   name: 'lineType',
  //   inputType: InputTypes.RADIO,
  //   options: lineTypes,
  //   config: {
  //     default: lineTypes[0].value
  //   }
},
BUILTIN_FIELDS.EDGE_TYPE,
BUILTIN_FIELDS.EDGE_CURVE_OFFSET,
BUILTIN_FIELDS.EDGE_STYLE,
BUILTIN_FIELDS.EDGE_VISIBLE,
])

const edgeFields = defineFields()

const comboFields = defineFields([{
  label: '',
  name: 'name',
  inputType: InputTypes.TEXT,
  config: {
    required: true
  }
}, {
  label: '',
  name: 'fixed',
  inputType: InputTypes.SWITCH,
  config: {
    tip: '',
    required: true
  }
}, {
  label: '',
  name: 'width',
  inputType: InputTypes.NUMBER,
  config: {
    tip: '',
    minlength: 0,
    maxlength: 2000,
    default: 200,
    isVisible(e) {
      return e.data.fixed
    }
  }
}, {
  label: '',
  name: 'height',
  inputType: InputTypes.NUMBER,
  config: {
    minlength: 0,
    maxlength: 2000,
    default: 200,
    tip: '',
    isVisible(e) {
      return e.data.fixed
    }
  }
}])

export default {
  nodeFields,
  edgeFields,
  comboFields
}
